import { Component, OnInit, Input, Output, OnChanges, SimpleChanges, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { map, startWith, tap } from 'rxjs/operators';
import { TeacherSubstitution } from 'src/app/shared/models/teacher-substitution.model';
import { AuthService } from 'src/app/shared/services/auth.service';
import { TeacherSubstitutionService } from 'src/app/shared/services/teacher-substitution.service';

@Component({
  selector: 'erz-substitution-create',
  templateUrl: './substitution-create.component.html',
  styleUrls: ['./substitution-create.component.scss']
})
export class SubstitutionCreateComponent implements OnInit, OnChanges {

  settings: any;
  @Input('holderId') holderIdInput:Option<Number>;
  @Input() substitution:Option<TeacherSubstitution>;
  @Output('cancel') cancelled = new EventEmitter<void>();
  @Output('save') saved = new EventEmitter<void>();

  dateFrom = new FormControl(null, Validators.required);
  dateTo = new FormControl(null, Validators.required);
  remarks = new FormControl();
  holderId = new FormControl(null, Validators.required);
  substituteId = new FormControl(null, Validators.required);

  errorMessage$:Observable<Option<ReadonlyArray<string>>>;
  serverErrorMessage$ = new BehaviorSubject<Option<ReadonlyArray<string>>>(null);
  clientErrorMessage$:Observable<Option<ReadonlyArray<string>>>;

  form:FormGroup;
  showErrors:boolean = false;
  savingActive$:BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  constructor(
    public teacherSubstitutionService: TeacherSubstitutionService,
    public authService: AuthService,
    private translateService: TranslateService,
    public changeDetector: ChangeDetectorRef
  ) { }

  submit():void {
    if (!this.form.valid) {
      this.showErrors = true;
      return;
    }

    this.savingActive$.next(true);

    this.teacherSubstitutionService.create(
      this.holderId.value, 
      this.substituteId.value, 
      this.dateFrom.value, 
      this.dateTo.value,
      this.remarks.value
    ).subscribe(
      v => {
        this.savingActive$.next(false);
        this.saved.emit();
      },
      err => { 
        this.savingActive$.next(false);
        this.showErrors = true;

        this.serverErrorMessage$.next(err.Issues.map((v:any) => v.Message as string));
      }
    );
  }
  cancel():void {
    this.cancelled.emit();
  }

  ngOnInit(): void {
    //this.holderId.setValue(this.authService.personId);
    this.holderId.setValue(this.holderIdInput);

    this.form = new FormGroup({
      'DateFrom': this.dateFrom,
      'DateTo': this.dateTo,
      'Substitute': this.substituteId,
      'Holder': this.holderId,
      'Remarks': this.remarks
    });

    this.clientErrorMessage$ = this.form.valueChanges.pipe(
      startWith(null),
      //map(v => this.getAllFormControlErrors()),
      map(v => [])
    );

    this.errorMessage$ = combineLatest([this.serverErrorMessage$, this.clientErrorMessage$]).pipe(
      map(v => (v[0] ?? []).concat(v[1] ?? [])),
      map(v => v.length == 0 ? null : v)
    );
  }

  private getAllFormControlErrors():Array<string> {
    return Object.entries(this.form.controls).flatMap(
      ([fieldName, control]) => Object.entries(control.errors ?? {}).filter(([key, value]) => value).map(([key, value]) => key).map(
        (error) => `${this.translate(["substitution-model", fieldName])}: ${this.translate(['global', 'validation-errors', error])}`
      )
    )
  }

  getFormControlErrors(fieldName:string):Array<string> {
    let control = this.form.get(fieldName);
    if (control == null) return [];

    return Object.entries(control.errors ?? {}).filter(([key, value]) => value).map(([key, value]) => key).map(
        (error) => `${this.translate(['global', 'validation-errors', error])}`
    );
  }

  private translate(path:string[]):string {
    return this.translateService.instant(path.join('.'));
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['substitution'] && this.substitution != null) {
      this.dateFrom.setValue(this.substitution.DateFrom);
      this.dateTo.setValue(this.substitution.DateTo);
      this.holderId.setValue(this.substitution.HolderId);
      this.substituteId.setValue(this.substitution.SubstituteId);
    }
  }
}
