import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { BehaviorSubject, Observable, combineLatest } from 'rxjs';
import { map, mergeMap, repeatWhen, switchMap, tap } from 'rxjs/operators';
import { TeacherSubstitution } from 'src/app/shared/models/teacher-substitution.model';
import { AuthService } from 'src/app/shared/services/auth.service';
import { TeacherSubstitutionService } from 'src/app/shared/services/teacher-substitution.service';

@Component({
  selector: 'erz-substitution-list-current',
  templateUrl: './substitution-list-current.component.html',
  styleUrls: ['./substitution-list-current.component.scss']
})
export class SubstitutionListCurrentComponent implements OnInit {

  settings: any;
  constructor(
    public teacherSubstitutionService: TeacherSubstitutionService,
    public changeDetector: ChangeDetectorRef,
    public authService: AuthService
  ) { }

  isLoading:Boolean = true;
  currentSubstitutionId:Option<number> = null; 
  update$:BehaviorSubject<Date> = new BehaviorSubject<Date>(new Date());
  substitutions$:Observable<ReadonlyArray<{substitution:TeacherSubstitution,isActive:boolean}>>;
  workingId$:BehaviorSubject<number> = new BehaviorSubject<number>(-1);

  ngOnInit(): void {
    const compare = (a:any, b:any) => a == b ? 0 : (a ?? "") > (b ?? "") ? -1 : 1;
    const compareColumns = (a:any, b:any, selectors:[selector:((v:any)=>any),ascending:boolean][]) => {
      for (let [selector, ascending] of selectors) {
        let sort = compare(selector(a), selector(b));
        if (ascending) sort *= -1;
        if (sort != 0) {
          return sort;
        }
      }
      return 0;
    }
    const createCompareColumnsFunc = (selectors:[selector:(v:any)=>any, ascending:boolean][]) => (a:any, b:any) => compareColumns(a, b, selectors);

    //this.currentSubstitutionId = this.authService.substitutionId;
    this.substitutions$ = this.authService.substitutionId$.pipe(
      tap(v => this.currentSubstitutionId = v),
      switchMap(v => this.teacherSubstitutionService.getCurrentList()),
      map(v => Array.from(v).sort(createCompareColumnsFunc([[(w:TeacherSubstitution) => this.currentSubstitutionId == w.Id, false], [(w:TeacherSubstitution) => w.DateFrom, true]]))),
      map(v => v.map(w => {return {substitution: w, isActive: w.DateFrom < new Date()}})),
      tap(v => {
        this.isLoading = false;
        this.changeDetector.markForCheck();
      })
    );
  }

  startSubstitution(substitutionId:number) {
    this.workingId$.next(substitutionId);
    this.teacherSubstitutionService.start(substitutionId).subscribe(v => {
      this.currentSubstitutionId = substitutionId;
      this.changeDetector.markForCheck();
      this.update$.next(new Date());
      this.workingId$.next(-1);
    });
  }

  stopSubstitution(substitutionId:number) {
    this.workingId$.next(substitutionId);
    this.teacherSubstitutionService.stop(substitutionId).subscribe(v => {
      this.currentSubstitutionId = null;
      this.changeDetector.markForCheck();
      this.update$.next(new Date());
      this.workingId$.next(-1);
    });
  }
}
