import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthAdminGuard } from '../auth.admin.guard';
import { AuthGuard } from '../auth.guard';
import { PageAdminSubstitutionCreateComponent } from './components/page-admin-substitution-create/page-admin-substitution-create.component';
import { PageAdminSubstitutionEditComponent } from './components/page-admin-substitution-edit/page-admin-substitution-edit.component';
import { PageAdminSubstitutionListComponent } from './components/page-admin-substitution-list/page-admin-substitution-list.component';
import { PageSubstitutionCreateComponent } from './components/page-substitution-create/page-substitution-create.component';
import { PageSubstitutionEditComponent } from './components/page-substitution-edit/page-substitution-edit.component';
import { PageSubstitutionListCurrentComponent } from './components/page-substitution-list-current/page-substitution-list-current.component';
import { PageSubstitutionListComponent } from './components/page-substitution-list/page-substitution-list.component';
import { SubstitutionListComponent } from './components/substitution-list/substitution-list.component';

const routes: Routes = [
  {
    path: 'assign',
    canActivate: [AuthGuard],
    component: PageSubstitutionListComponent,
  },
  {
    path: 'assign/edit/:id',
    canActivate: [AuthGuard],
    component: PageSubstitutionEditComponent
  },
  {
    path: 'assign/create',
    canActivate: [AuthGuard],
    component: PageSubstitutionCreateComponent
  },
  {
    path: 'admin/create',
    canActivate: [AuthAdminGuard],
    component: PageAdminSubstitutionCreateComponent
  },
  {
    path: 'admin/edit/:id',
    canActivate: [AuthAdminGuard],
    component: PageAdminSubstitutionEditComponent
  },
  {
    path: 'admin',
    canActivate: [AuthAdminGuard],
    component: PageAdminSubstitutionListComponent
  },
  {
    path: 'execute',
    canActivate: [AuthGuard],
    component: PageSubstitutionListCurrentComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SubstitutionsRoutingModule {}
