import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { BehaviorSubject, Observable, combineLatest } from 'rxjs';
import { mergeMap, repeatWhen, share, switchMap, tap, map, filter, count, delay } from 'rxjs/operators';
import { TeacherSubstitution } from 'src/app/shared/models/teacher-substitution.model';
import { TeacherSubstitutionService } from 'src/app/shared/services/teacher-substitution.service';

const INITIAL_PAGE_SIZE = 50;

@Component({
  selector: 'erz-substitution-list',
  templateUrl: './substitution-list.component.html',
  styleUrls: ['./substitution-list.component.scss']
})
export class SubstitutionListComponent implements OnInit {

  settings: any;
  constructor(
    public teacherSubstitutionService: TeacherSubstitutionService,
    public changeDetector: ChangeDetectorRef
  ) { }

  @Input('sort-column')
  get sortColumn():string { return this.sortColumn$.value ?? ""; }
  set sortColumn(v:string) {
    this.sortColumn$.next(v);
  }
  @Input('sort-direction')
  get sortDirection():boolean { return this.sortDirection$.value; }
  set sortDirection(v:boolean) {
    this.sortDirection$.next(v);
  }

  sortColumn$:BehaviorSubject<Option<string>> = new BehaviorSubject<Option<string>>(null);
  sortDirection$:BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  @Input('filter')
  get filter():string { return this.filter$.value; }
  set filter(v:string) { this.filter$.next(v ?? ""); }
  filter$:BehaviorSubject<string> = new BehaviorSubject<string>("");

  @Input('show-holder') showHolder:Boolean = false;
  @Input('edit-link') editLinkFunc:Option<(a:string)=>string[]> = v => ['edit', v];
  //@Input('substitutions$') substitutionsInput$:Observable<ReadonlyArray<TeacherSubstitution>>;

  @Input('substitutions$')
  substitutionsInput$:Option<Observable<ReadonlyArray<TeacherSubstitution>>>;
  //get substitutions():Option<ReadonlyArray<TeacherSubstitution>> { return this.substitutionsInput$.value; }
  //set substitutions(v:Option<ReadonlyArray<TeacherSubstitution>>) { this.substitutionsInput$.next(v); this.update$.next(new Date()); }
  //useSubstitutionsInput$:BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  isLoading:Boolean = true;
  update$:BehaviorSubject<Date> = new BehaviorSubject<Date>(new Date());
  substitutions$:Observable<ReadonlyArray<TeacherSubstitution>>;
  listSize$:BehaviorSubject<number> = new BehaviorSubject<number>(INITIAL_PAGE_SIZE);
  totalCount$:BehaviorSubject<number> = new BehaviorSubject<number>(0);

  ngOnInit(): void {
    this.substitutions$ = combineLatest([
        this.listSize$,
        this.update$.pipe(
          delay(1000),
          switchMap(
            v => this.substitutionsInput$ ?? this.teacherSubstitutionService.getMyList()
          ),
          share(),
        ),
        this.filter$.pipe(
          tap(() => this.listSize$.next(INITIAL_PAGE_SIZE)),
          map(v => v.toLowerCase())
        ),
        this.sortColumn$,
        this.sortDirection$
    ]).pipe(
      map(v => {
        let [listSize, items, filter, sortColumn, sortDirection] = v;

        const compare = (a:any, b:any) => a == b ? 0 : (a ?? "") > (b ?? "") ? -1 : 1;
        const compareColumns = (a:any, b:any, columns:string[]) => {
          for (let column of columns) {
            let sort = compare(a[column], b[column]);
            if (sort != 0) {
              return sort;
            }
          }
          return 0;
        }
        const createCompareColumnsFunc = (columns:string[]) => (a:any, b:any) => compareColumns(a, b, columns);

        if (sortColumn == null) {
          items = Array.from(items).sort(createCompareColumnsFunc(['DateFrom', 'Substitute'])).reverse();
        } else {
          let col = sortColumn;

          let itemArray = Array.from(items).sort(createCompareColumnsFunc([col, 'DateFrom', 'Substitute']));
          if (sortDirection) {
            items = itemArray.reverse();
          } else {
            items = itemArray;
          }
        }

        this.totalCount$.next(items.length);

        return {
          listSize, 
          items, 
          filter
         } as const
      }),
      map(({listSize, items, filter}) => [listSize, items.filter(w => filter == "" || w.Holder.toLowerCase().includes(filter) || w.Substitute.toLowerCase().includes(filter))] as const),
      map(v => v[1].slice(0, v[0])),
      tap(v => {
        this.isLoading = false;
        this.changeDetector.markForCheck();
      }),
    );

  }

  sortBy(column:string): void {
    if (this.sortColumn == column) {
      this.sortDirection = !this.sortDirection;
    } else {
      this.sortColumn = column;
      this.sortDirection = true;
    }
    this.changeDetector.markForCheck();
  }

  onScroll(): void {
    this.listSize$.next(this.listSize$.value + 10);
  }

  deleteSubstitution(substitutionId:number) {
    this.teacherSubstitutionService.delete(substitutionId).subscribe(v => {
      this.update$.next(new Date());
    });
  }
}
