import { Component, OnInit, Input, Output, OnChanges, SimpleChanges, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { Observable, combineLatest, BehaviorSubject } from 'rxjs';
import { map, combineAll, switchMap, tap, delay, share } from 'rxjs/operators';
import { TeacherSubstitution } from 'src/app/shared/models/teacher-substitution.model';
import { TeacherSubstitutionService } from 'src/app/shared/services/teacher-substitution.service';

@Component({
  selector: 'erz-page-admin-substitution-edit',
  templateUrl: './page-admin-substitution-edit.component.html',
  styleUrls: ['./page-admin-substitution-edit.component.scss']
})
export class PageAdminSubstitutionEditComponent implements OnInit {

  isLoading:Boolean = true;
  settings: any;
  substitution$:Observable<Option<TeacherSubstitution>>;

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public teacherSubstitutionService: TeacherSubstitutionService,
    public changeDetector: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.substitution$ = this.route.paramMap.pipe(
      tap(v => {
        this.isLoading = true;
        this.changeDetector.markForCheck();
      }),
      //delay(100),
      map(v => v.get('id')),
      map(v => parseInt(v as string)),
      switchMap(id => this.teacherSubstitutionService.get(id)),
      tap(v => {
        this.isLoading = false;
        this.changeDetector.markForCheck();
      }),
      share()
    );
    this.substitution$.subscribe(v => null);
  }

  navigateToList():void {
    this.router.navigate(['/substitutions/admin']);
  }
}
